

































































































































import {Component} from "vue-property-decorator";
import {mixins} from "vue-class-component";

// import {AccountDataModel} from "@/store/models";
import account from "@/store/modules/accounts";
import DataMixins from "@/data-mixins";
import {AccountDataModel} from "@/store/models";

@Component({
  name: 'AccountDetail', filters: {
    currency_format: (amount: number) => { return new Intl.NumberFormat('en-GB', {style: 'currency', currency: 'EUR'}).format(amount / 100) },
    date_format: (value: string) => {
      return new Date(value).toLocaleDateString("en-US", {
        hour12: true,
        hour: '2-digit',
        minute: '2-digit',
        weekday: 'short',
        year: 'numeric',
        month: 'short',
        day: 'numeric'
      })
    }
  }
})
export default class AccountDetail extends mixins(DataMixins) {
  accountDetails: AccountDataModel | null | undefined = null;
  accountLoading = true;
  isPaginated = true;
  isPaginationSimple = false;
  isPaginationRounded = false;
  paginationPosition = 'bottom';
  defaultSortDirection = 'asc';
  sortIcon = 'arrow-up';
  sortIconSize = 'is-small';
  currentPage = 1;
  perPage = 20

  mounted(): void {
    account.fetchSingleAccountInfo(this.$route.params.accountId).then(() => {
      setTimeout(() => {
        this.accountDetails = account.singleAccount;
        this.accountLoading = false
      }, 20);
    })
  }


}
